import React from 'react'
import {Carousel} from 'antd'
import './style.css'

import img1 from '../../assets/img/wallhaven-p8r1e9.jpg';
import img2 from '../../assets/img/wallhaven-e7zyy8.jpg';
import img3 from '../../assets/img/wallhaven-6k9e7q.jpg';
import img4 from '../../assets/img/photo.jpg';

const imgs = [
  img1,
  img2,
  img3,
  img4,
]


class Home extends React.Component {
  render() {
    return (
      <div style={styles.bg} className='home'>
        <Carousel arrows effect='fade' className='size'>
          {imgs.map(item=><div key={item}><div className='size' style={{backgroundImage:`url(${item})`}}/></div>)}
          {/*img*/}
        </Carousel>
      </div>
    )
  }
}

const styles = {
  bg:{
    position:'absolute',
    top:0,
    left:0,
    width:'100%',
    height:'calc(100vh - 64px)'
  }
}

export default Home


